import React, { useState, useEffect, useCallback, useRef } from "react";
import "./settingsDisplayPOI.css";
export const SettingsDisplayPOI = (props) => {
  const handleChange = (event) => {
    // updating an object instead of a Map
    props.setDisplayPoiType({
      ...props.displayPoiType,
      [event.target.name]: event.target.checked,
    });
  };

  useEffect(() => {
    console.log("props.displayPoiType: ", props.displayPoiType);
  }, [props.displayPoiType]);

  const markerIcon =
    "https://www.flaticon.com/svg/static/icons/svg/787/787535.svg";

  const benchIcon =
    "https://www.flaticon.com/svg/static/icons/svg/183/183449.svg";

  const tableIcon =
    "https://www.flaticon.com/svg/static/icons/svg/3113/3113709.svg";

  const areaIcon =
    "https://www.flaticon.com/svg/static/icons/svg/972/972263.svg";

  const historyIcon =
    "https://www.flaticon.com/svg/static/icons/svg/1277/1277727.svg";

  const statueIcon =
    "https://www.flaticon.com/svg/static/icons/svg/3205/3205261.svg";

  const checkboxes = [
    {
      name: "bench",
      key: "bench",
      label: "住驻住",
      icon: benchIcon,
    },
    {
      name: "area",
      key: "area",
      label: "专",
      icon: areaIcon,
    },
    {
      name: "table",
      key: "table",
      label: "砖",
      icon: tableIcon,
    },
    {
      name: "statue",
      key: "statue",
      label: "驻住",
      icon: statueIcon,
    },
    {
      name: "lookout",
      key: "lookout",
      label: "转爪驻转",
      icon: markerIcon,
    },
    {
      name: "history",
      key: "history",
      label: "转专 住专",
      icon: historyIcon,
    },
  ];

  return (
    <div className="display-poi">
      <lable> 爪 驻</lable> <br />
      {checkboxes.map((item) => (
        <label key={item.key}>
          {item.label}
          <img src={item.icon} alt={item.name} className="checkbox-icon"></img>
          <input
            type="checkbox"
            name={item.name}
            checked={props.displayPoiType[item.name]}
            onChange={handleChange}
          />
        </label>
      ))}
    </div>
  );
};
